  font-weight: var(--normal);
  font-size: var(--body-2);
  text-align: center;
  color: var(--black-100);
  cursor: pointer;
  transition: color .3s;
  position: relative;
}

.word:after {
  content: "";
  width: 93px;
  height: 0;
  transition: all .3s;
  display: none;
  position: absolute;
  left: 44px;
  transform: translateX(-50%);
}

.word.active {
  color: var(--background);
}

.word.active:after {
  border: 1px solid var(--background);
  display: block;
}

.word:before {
  content: "";
  width: 7px;
  height: 7px;
  background-color: var(--background);
  border-radius: 100%;
  display: none;
  position: absolute;
  top: 18px;
  left: 44px;
  transform: translate(-50%, -50%);
}

.word.active:before {
